* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
    height: 100%;
    background-color: rgb(32, 32, 32);
    color: white;
}

body {
    display: flex;
    flex-direction: column;
    padding: 2rem;
}

.container {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form-div {
    display: flex;
    justify-content: flex-end;
    margin-right: 2rem;
}

form {
    position: relative;
}

input {
    font-size: 1rem;
    padding: 0.2rem 0.3rem;
    border-radius: 3px;
    border: 1px solid grey;
}

input:focus {
    outline: none;
}

button.search {
    all: unset;
    font-size: 1rem;
    padding: 0.2rem 0.3rem;
    border-radius: 3px;
    border: 1px solid grey;
    color: rgb(212, 212, 212);
    cursor: pointer;
}

button.search:active {
    transform: scale(0.95);
}

#error {
    position: absolute;
    top: 30px;
    left: 25px;
    color: rgb(255, 0, 0);
    font-size: 0.95rem;
}

.hide {
    display: none;
}

p.city-title {
    font-size: 3rem;
    margin: 1rem;
    font-family: 'Rubik';
}

.main {
    flex: 2;
    width: 550px;
    display: flex;
    padding: 1rem;
    align-items: center;
    gap: 1rem;
    font-family: 'Rubik';
}

.main img {
    width: 300px;
}

.main p {
    font-size: 6rem;
}

.units {
    display: flex;
    flex-direction: column;
    
}

.units button {
    all: unset;
    cursor: pointer;
    font-size: 2rem;
    color: rgb(86, 86, 86);
}

button.active {
    color: white;
}

#metric {
    border-bottom: 1px solid  rgb(71, 71, 71) ;
    padding-right: 5px;
}

.additional {
    flex: 1;
    width: 550px;
    background-color: rgba(103, 105, 107,0.2);
    color: rgb(104, 104, 104);
    font-size: 1.3rem;
    padding: 1rem;
    border-radius: 0px 0px 3px 3px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-family: 'Inter';
    border-top: 1px solid rgb(130, 130, 130);
}

.data {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.date-box {
    font-size: 1.7rem;
}

.loading {
    margin-top: 15rem;
    text-align: center;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA,0BAA0B;AAC1B;EACE,kBAAkB,EAAE,sGAAsG;EAC1H,oBAAoB;EACpB,kBAAkB;EAClB,gBAAgB;EAChB,4DAAiE,EAAE,4DAA4D;AACjI;;AAEA,0BAA0B;AAC1B;EACE,kBAAkB,EAAE,sGAAsG;EAC1H,oBAAoB;EACpB,kBAAkB;EAClB,gBAAgB;EAChB,4DAAiE,EAAE,4DAA4D;AACjI;;;AAGA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;;IAEI,YAAY;IACZ,iCAAiC;IACjC,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,OAAO;IACP,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,sBAAsB;IACtB,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,UAAU;IACV,eAAe;IACf,sBAAsB;IACtB,kBAAkB;IAClB,sBAAsB;IACtB,yBAAyB;IACzB,eAAe;AACnB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,qBAAqB;IACrB,kBAAkB;AACtB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,oBAAoB;AACxB;;AAEA;IACI,OAAO;IACP,YAAY;IACZ,aAAa;IACb,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,oBAAoB;AACxB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,sBAAsB;;AAE1B;;AAEA;IACI,UAAU;IACV,eAAe;IACf,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,0CAA0C;IAC1C,kBAAkB;AACtB;;AAEA;IACI,OAAO;IACP,YAAY;IACZ,yCAAyC;IACzC,yBAAyB;IACzB,iBAAiB;IACjB,aAAa;IACb,8BAA8B;IAC9B,aAAa;IACb,mBAAmB;IACnB,6BAA6B;IAC7B,oBAAoB;IACpB,wCAAwC;AAC5C;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB","sourcesContent":["/* rubik-regular - latin */\n@font-face {\n  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */\n  font-family: 'Rubik';\n  font-style: normal;\n  font-weight: 400;\n  src: url('./fonts/rubik-v31-latin-regular.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */\n}\n\n/* inter-regular - latin */\n@font-face {\n  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-displ